import React, { useState, useCallback, createContext, useContext } from 'react';

import icComments from './assets/images/ic-comments.png';
import imgKkyLogo from './assets/images/kky-logo.jpg';

import ReactPlayer from 'react-player';

import Modal from 'react-bootstrap/Modal';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';

import './styles.scss';

import content from './content/content';
import Container from 'react-bootstrap/Container';
import Carousel from 'react-bootstrap/Carousel';
import { Fab } from './components';
import CardColumns from 'react-bootstrap/CardColumns';

const ModalContext = createContext(null);

function getPerson(personId) {
  return content.persons[personId];
}
function getMediaUrl(mediaId) {
  return content.media[mediaId].url;
}
function getComments(commentsId) {
  return content.comments[commentsId];
}

function Header() {
  return (
    <Carousel className="mb-2">
      <Carousel.Item>
        {/*<div className="d-block w-100" style={{height: "50vh", backgroundImage: `url(${imgKkyLogo})`, backgroundPosition: "center", backgroundRepeat: "no-repeat", backgroundSize: "contain"}} />*/}
        <div className="w-100 text-center" style={{ opacity: 0.3 }}>
          <img
            style={{ maxWidth: 300 }}
            className="my-5"
            src={imgKkyLogo}
            alt="First slide"
          />
        </div>
        <Carousel.Caption style={{ color: 'black' }}>
          <h3>Kavalam Kudumbayogam</h3>
          <p>Anniversary Celebrations 2020</p>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
  );
}
function CardMedia({
  type,
  description,
  mediaId,
  currentMediaId,
  onPlay,
  ...otherProps
}) {
  const text = (
    <>
      <ReactPlayer
        className={type === 'card-video' ? 'video-player' : 'audio-player'}
        url={getMediaUrl(mediaId)}
        controls={true}
        width={'100%'}
        playing={currentMediaId === mediaId}
        onPlay={() => onPlay(mediaId)}
      />
      <div className="mt-2" />
      {description}
    </>
  );
  return <CardText {...otherProps} text={text} />;
}

function Comment({ personId, text }) {
  const person = getPerson(personId);
  return (
    <blockquote className="blockquote mb-3">
      <p>{text}</p>
      <footer className="blockquote-footer">{person.name}</footer>
    </blockquote>
  );
}

function CardText({ personId, title, commentsId, text }) {
  const { promptModal } = useContext(ModalContext);
  const person = getPerson(personId);
  const comments = getComments(commentsId);

  function showComments() {
    promptModal(
      <>
        <Modal.Header closeButton>
          <Modal.Title>{title}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {comments.map((comment) => (
            <Comment {...comment} />
          ))}
        </Modal.Body>
      </>
    );
  }
  return (
    <Card className="kky-card kky-card-text text-left">
      <Card.Body>
        <Card.Title className="kky-card__title mb-1">{title}</Card.Title>
        <Card.Text className="kky-card__author mb-2 text-muted">
          {person.name}
        </Card.Text>

        <Card.Text className="kky-card-text__description text-muted">
          {text}
        </Card.Text>
        {comments && comments.length > 0 && (
          <div className="text-right">
            <Card.Link href="#" onClick={showComments}>
              <img className="ic-comments" src={icComments} alt={'Comments'} />
            </Card.Link>
          </div>
        )}
      </Card.Body>
    </Card>
  );
}

function TimelineItem({ type, currentMediaId, onPlay, ...otherProps }) {
  if (type === 'card-text') {
    return <CardText {...otherProps} />;
  } else if (type === 'card-audio' || type === 'card-video') {
    const { description, mediaId, ...otherProps2 } = otherProps;
    return (
      <CardMedia
        type={type}
        description={description}
        mediaId={mediaId}
        currentMediaId={currentMediaId}
        onPlay={onPlay}
        {...otherProps2}
      />
    );
  } else {
    return <code>Unknown timeline item: {type}</code>;
  }
}

export default function App() {
  const [modalContent, setModalContent] = useState(null);
  const [currentMediaId, setCurrentMediaId] = useState(null);

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);

  const promptModal = useCallback((content) => {
    setModalContent(content);
    setShow(true);
  }, []);

  function showFeedbackForm() {
    promptModal(
      <>
        <Modal.Header closeButton>
          <Modal.Title>Feedback form</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <iframe
            scrolling="no"
            src="https://docs.google.com/forms/d/e/1FAIpQLSdtqep0T531NXAQYwTZvH31JNL_eDhaxevmgJn6zMMla5AxkA/viewform?embedded=true"
            width="100%"
            height="1000"
            frameBorder="0"
            marginHeight="0"
            marginWidth="0"
          >
            Loading…
          </iframe>
        </Modal.Body>
      </>
    );
  }
  return (
    <ModalContext.Provider value={{ promptModal }}>
      <Header />
      <Container fluid>
        <CardColumns>
          {content.timeline.map((data, i) => (
            <TimelineItem
              key={i}
              {...data}
              onPlay={(mediaId) => setCurrentMediaId(mediaId)}
              currentMediaId={currentMediaId}
            />
          ))}
        </CardColumns>
        <Modal show={show} onHide={handleClose}>
          {modalContent}
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </Container>
      <Fab onClick={showFeedbackForm} />
    </ModalContext.Provider>
  );
}
